<template>
<div class="game-results-page">
  <!--  1      -->
  <LogoHeader />

  <!--  ,    -->
  <div class="menu">
    <div class="menu-item" :class="{ 'active-menu-item': activeMenu === '/game/schedule' }" @click="navigateTo('/game/schedule')"> </div>
    <div class="menu-item" :class="{ 'active-menu-item': activeMenu === '/game/results' }" @click="navigateTo('/game/results')"> </div>
  </div>

  <!--     -->
  <div class="game-results-content">
    <h2>  </h2>

    <!--    ( ,  ,  ) -->
    <div class="header-container">
      <div class="select-container">
        <select v-model="selectedSeasonId" @change="fetchMatches(0)">
          <option v-for="season in seasons" :key="season.seasonId" :value="season.seasonId">
            {{ season.seasonName }}
          </option>
      </select>
      </div>

      <!--   -->
      <div class="round-navigation">
        <img src="@/assets/img/game/nav-icon-previous.png"
              alt="Previous"
              @click="changeRound(-1)"
              :class="{ disabled: matchRoundId <= 1 }" />

        <!--     '' /   ''  -->
        <h2>
          {{ totalPages === 1 && !matchRoundId ? '' : (matches.length > 0 ? matches[0].matchRoundId + ' ' : '') }}
        </h2>
        <img src="@/assets/img/game/nav-icon-next.png"
              alt="Next"
              @click="changeRound(1)"
              :class="{ disabled: matchRoundId >= maxRound }" />
      </div>

      <!--    -->
      <div class="round-select-box">
        <select v-model="matchRoundId" @change="fetchMatches(0)">
          <option disabled value=""> </option>
          <option v-for="round in 6" :key="round" :value="round">
            {{ round }}
          </option>
        </select>
      </div>

      <!--   -->
      <button class="show-all-btn" @click="showAllMatches"></button>
    </div>

    <table class="match-table">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="match in matches" :key="match.matchId">
          <td>{{ formatDate(match.matchDate) }}</td>
          <td>{{ match.tableIndex }}</td>
          <td>
            <span class="home-team">MMS GS</span>
            <span class="score">{{ match.matchSetScore }}:{{ match.matchOpponentTeamSetScore }}</span>
            <span class="away-team">{{ match.teamName }}</span>
          </td>
          <td>{{ formatTime(match.matchDate) }}</td>
          <td>{{ formatLocation(match) }}</td>
          <td>{{ match.matchRoundId }} </td>
        </tr>
      </tbody>
    </table>

  </div>
</div>
</template>

<script>
import LogoHeader from '../common/LogoHeader.vue';

export default {
  name: 'AppResults',
  components: {
    LogoHeader
  },
  data() {
    return {
      activeMenu: this.$route.path, //   
      //  
      seasons: [
        { seasonId: 1, seasonName: ' 2023-2024 V-' },
        { seasonId: 2, seasonName: ' 2024-2025 V-' },
      ],
      selectedSeasonId: 2, //    2024-2025 V- 
      matches: [], //    
      currentPage: 0, //  
      totalPages: 0, //   
      matchRoundId: 2, //  
      maxRound: 6, //   
    };
  },
  watch: {
    //    activeMenu 
    $route(to) {
      this.activeMenu = to.path;
    },
  },
  methods: {
    //    
    navigateTo(route) {
      this.$router.push(route);
      //      
      this.activeMenu = route;
    },

    //  
    changeRound(step) {
      const newRound = this.matchRoundId + step;
      if (newRound >= 1 && newRound <= this.maxRound) {
        this.matchRoundId = newRound;
        this.fetchMatches(); //    
      }
    },

    //      
    async fetchMatches(page = 0) {
      try {
        const response = await this.$axios.get(`http://localhost:4000/game/results`, {
          params: {
            seasonId: this.selectedSeasonId,
            page: page,
            size: 6, // 6  
            matchRoundId: this.matchRoundId, //  
            sort: 'matchDate,asc', // matchDate   
          }
        });
        //   () 
        //     index + 1   1  
        this.matches = response.data.content.map((match, index) => ({
          ...match,
          tableIndex: index + 1 + page * 6
        }));
        this.totalPages = response.data.totalPages;
        this.currentPage = page;
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    },

    //  -     
    async showAllMatches() {
      this.matchRoundId = ''; //   
      try {
        const response = await this.$axios.get(`http://localhost:4000/game/results`, 
        {
          params: {
          seasonId: this.selectedSeasonId,
          page: 0,
          size: 1000, //         
          sort: 'matchDate,asc', // matchDate   
          }
        });
        this.matches = response.data.content.map((match, index) => ({
          ...match,
          tableIndex: index + 1,
        }));
        this.totalPages = 1; //    1 
        this.currentPage = 0;
      } catch (error) {
        console.error("Error fetching all match data:", error);
      }
    },

    //  
    goToPage(page) {
      if (page >= 0 && page < this.totalPages) {
        this.fetchMatches(page);
      }
    },

    //  
    formatDate(date) {
      const d = new Date(date);
      const month = String(d.getMonth() + 1).padStart(2, '0'); //  2 
      const day = String(d.getDate()).padStart(2, '0'); //  2 
      return `${month}.${day} (${['', '', '', '', '', '', ''][d.getDay()]})`;
    },
    //  
    formatTime(date) {
      const d = new Date(date);
      return `${d.getHours()}:${d.getMinutes().toString().padStart(2, '0')}`;
    },
    //   
    formatMatchResult(match) {
      return `MMS GS   ${match.matchSetScore}:${match.matchOpponentTeamSetScore}   ${match.teamName}`;
    },
    //  
    formatLocation(match) {
      return match.matchStadium === 'HOME' ? '' : match.teamStadium;
    }
  },
  mounted() {
    this.fetchMatches();  //       
  }
};
</script>

<style scoped>
.game-results-page {
  padding-top: var(--header-height);
  padding-bottom: var(--footer-height);
  text-align: center;
}

/*  */
.menu {
  display: flex;
  justify-content: center;
  gap: 50px;
  padding-bottom: 20px;
}

.menu-item {
  width: 120px;
  padding: 10px 0;
  position: relative;
  cursor: pointer;
  font-weight: bold;
  color: #565656;
}

.menu-item::after {
  content: "";
  position: absolute;
  bottom: -5px; /*       */
  left: 0;
  width: 100%;  /*   div   */
  height: 3px;
  background-color: black;
  transform: scaleX(0); /*    */
  transition: transform 0.3s ease;
}

.active-menu-item {
  color: black;  /*     black  */
}

.active-menu-item::after {
  transform: scaleX(1);  /*     */
}

.menu-item:hover::after {
  transform: scaleX(1);  /*     */
}

/*   */
.game-results-content {
  padding-top: 30px;
  padding-bottom: 100px;
}

.header-container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  margin-bottom: 8px;
}

.select-container {
  position: absolute;
  left: 0px;
  display: flex;
}

select {
  /* background-color: #f8f9fa; */
  -webkit-appearance: none; /*    */
  -moz-appearance: none; /* Firefox    */
  appearance: none; /*    */
  border: 1px solid #ccc;
  padding: 10px 40px;
  padding-right: 50px;
  border-radius: 5px;
  font-size: 16px;
  font-weight: bold;
  color: #333;
  cursor: pointer;
  background-image: url('@/assets/img/game/select-icon-down.png'); /*    */
  background-repeat: no-repeat;
  background-position: right 15px center; /*   */
  background-size: 12px; /*   */
  margin-top: 0;
}

.round-navigation {
  display: flex;
  justify-content: center;
  align-items: center;
}

.round-navigation img {
  width: 40px;
  height: 40px;
  margin: 0 20px;
  cursor: pointer;
  transition: opacity 0.3s; /*     */
}

.round-navigation img.disabled {
  cursor: not-allowed;
  pointer-events: none;
  opacity: 0.5;
}

.round-select-box {
  position: absolute;
  right: 150px;
  top: 50%;
  transform: translateY(-50%);
}

.show-all-btn {
  border: 1px solid #ccc;
  padding: 10px 30px;
  border-radius: 5px;
  font-size: 16px;
  font-weight: bold;
  color: #333;
  cursor: pointer;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}

/*   */
.match-table {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  border-collapse: collapse;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.match-table th, .match-table td {
  border: 1px solid #ccc;
  text-align: center;
  padding: 15px;
}

.match-table th {
  background-color: #f5f5f5;
  font-weight: bold;
}

.home-team, .away-team {
  margin-right: 10px; /*    */
}
.score {
  margin: 0 13px; /*     */
}
</style>