<template>
  <div class="game-schedule-page">
    <!--  1      -->
    <LogoHeader />

    <!--  ,    -->
    <div class="menu">
      <div class="menu-item" :class="{ 'active-menu-item': activeMenu === '/game/schedule' }" @click="navigateTo('/game/schedule')"> </div>
      <div class="menu-item" :class="{ 'active-menu-item': activeMenu === '/game/results' }" @click="navigateTo('/game/results')"> </div>
    </div>
  
    <!--     -->
    <div class="game-schedule-content">
      <h2 class="game-schedule-title">  </h2>
    <Calendar :events="events" calendarMode="total" />
    </div>
  </div>
</template>
  
<script>
import LogoHeader from '../common/LogoHeader.vue';
import Calendar from '../common/Calendar.vue';

export default {
    name: 'AppSchedule',
    components: {
      LogoHeader,
      Calendar,
  },    
  data() {
    return {
      //   
      activeMenu: this.$route.path,
      // Calendar    
      events: []
    };
  },
  mounted() {
    this.fetchEvents();
  },
  watch: {
    //    activeMenu 
    $route(to) {
      this.activeMenu = to.path;
    }
  },
  methods: {
    navigateTo(route) {
      this.$router.push(route);
      this.activeMenu = route; //      
    },
    async fetchEvents() {
      try {
        // API   
        const response = await this.$axios.get('http://localhost:4000/game/schedule/total');
        // events  response 
        this.events = response.data;
      } catch (error) {
        console.error("Error fetching events:", error);
      }
    }
  }
}
</script>
  
<style scoped>
.game-schedule-page {
  padding-top: var(--header-height);
  padding-bottom: var(--footer-height);
  text-align: center;
}

/*  */
.menu {
  display: flex;
  justify-content: center;
  gap: 50px;
  padding-bottom: 20px;
}

.menu-item {
  width: 120px;
  padding: 10px 0;
  position: relative;
  cursor: pointer;
  font-weight: bold;
  color: #565656;
}

.menu-item::after {
  content: "";
  position: absolute;
  bottom: -5px; /*       */
  left: 0;
  width: 100%;  /*   div   */
  height: 3px;
  background-color: black;
  transform: scaleX(0); /*    */
  transition: transform 0.3s ease;
}

.active-menu-item {
  color: black;  /*     black  */
}

.active-menu-item::after {
  transform: scaleX(1);  /*     */
}

.menu-item:hover::after {
  transform: scaleX(1);  /*     */
}

/*   */
.game-schedule-content {
  padding-top: 30px;
  padding-bottom: 100px;
}

.game-schedule-title {
  padding-bottom: 20px;
}
</style>