<template>
    <div class="login-page">
      <!--  1      -->
      <LogoHeader />
      
      <!--   -->
      <div class="login-form">
             <h2></h2>
             <form @submit.prevent="login">
                 <div class="form-group">
                     <label for="userEmail"> </label>
                     <input type="email" id="userEmail" v-model="userEmail" required placeholder="ex) abc@aaa.com"/>
                 </div>
                 <div class="form-group">
                     <label for="userPassword"></label>
                     <input type="Password" id="userPassword" v-model="userPassword" required placeholder=" " />
                 </div>
                 <button type="submit" class="submit-button"></button>
             </form>
             <div class="links">
                 <router-link to="/signup"></router-link>
             </div>
         </div>
       </div>

 </template>
 
 <script>
 import LogoHeader from '../common/LogoHeader.vue';
 
 export default {
     name: 'LoginPage',
     components: {
         LogoHeader
     },
     data() {
         return {
            userEmail: '', //   
            userPassword: '', //   
             activeMenu: this.$route.path //   
         };
     },
     watch: {
         //    activeMenu 
         $route(to) {
             this.activeMenu = to.path;
         }
     },
     methods: {
        navigateTo(route) {
            this.$router.push(route);
            this.activeMenu = route; //      
        },
        saveUserInfo(token) {
            let payload = null;
            try {
            const base64Payload = token.split('.')[1]; //     (Payload)
            const base64 = base64Payload.replace(/-/g, '+').replace(/_/g, '/'); // Base64   
            const decodedPayload = decodeURIComponent(
                atob(base64)
                .split('')
                .map((c) => `%${('00' + c.charCodeAt(0).toString(16)).slice(-2)}`)
                .join('')
            );
            payload = JSON.parse(decodedPayload); // JSON   
            } catch (error) {
                console.error('  ', error);
            }
            //    
            sessionStorage.setItem('email', payload.username);
            sessionStorage.setItem('name', payload.name);
            sessionStorage.setItem('role', payload.role);
        },
        login(){
            //  status 
            const params = new URLSearchParams();
            params.append('userEmail', this.userEmail);

            this.$axios.post("/login/check-status", null, { params: params })
            .then((response) => {
                console.log(response);
                if(response.data === 'active.'){

                const user = {
                email: this.userEmail,
                password: this.userPassword,
            };
            this.$axios({
                method: "post",
                url: "/login",
                data: JSON.stringify(user),
                headers: {
                "Content-Type": "application/json",
                },
            }).then((response) => {
                if (response.status === 401) {
                    alert("    .");
                } else {
                let accessToken = response.headers.authorization;  //   
                sessionStorage.setItem("token", accessToken); //  sessionStorage 
                this.saveUserInfo(accessToken);
                this.$emit('loginSuccess');
                this.$axios.defaults.headers.common["Authorization"] = accessToken;
                alert(" ");
                const redirectTo = this.$route.query.from || '/';
                this.$router.push(redirectTo); //   
                }
            }).catch(() => {
                alert(" !!");
            });
                } else {
                    alert(" .");
                }
        })
        .catch((error) => {
          alert(error);
        });


        //     const user = {
        //         email: this.userEmail,
        //         password: this.userPassword,
        //     };
        //     this.$axios({
        //         method: "post",
        //         url: "/login",
        //         data: JSON.stringify(user),
        //         headers: {
        //         "Content-Type": "application/json",
        //         },
        //     }).then((response) => {
        //         if (response.status === 401) {
        //             alert("    .");
        //         } else {
        //         let accessToken = response.headers.authorization;  //   
        //         sessionStorage.setItem("token", accessToken); //  sessionStorage 
        //         this.saveUserInfo(accessToken);
        //         this.$emit('loginSuccess');
        //         this.$axios.defaults.headers.common["Authorization"] = accessToken;
        //         alert(" ");
        //         this.$router.replace("/");
        //         }
        //     }).catch(() => {
        //         alert(" !!");
        //     });
        // },
     }
    }
 }
 </script>
 
 <style scoped>
 .login-page {
     padding-top: var(--header-height);
     padding-bottom: var(--footer-height);
     text-align: center;
 }
 
 .login-form {
     max-width: 400px; /*    */
     margin: 50px auto; 
     padding: 20px; 
     background-color: #f9f9f9; 
     border-radius: 10px;
     border: 2px solid #60a191;
     box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1); 
 }
 
 .login-form h2 {
     margin-bottom: 30px; 
     color: #333; 
 }
 
 .form-group {
     margin-bottom: 30px; 
 }
 
 label {
     display: block; 
     margin-bottom: 5px; 
     color: #555; 
 }
 
 input[type="email"],
 input[type="password"] {
     width: 100%; 
     padding: 10px; 
     border: 1px solid #ccc; 
     border-radius: 5px; 
 }
 
 input[type="email"]:focus,
 input[type="password"]:focus {
     border-color: #4f8578; 
     outline: none; 
 }
 
 .submit-button {
    margin-top: 15px;
     width: 100%;
     padding: 10px; 
     background-color: #4f8578; 
     color: #ffffff; 
     border: none; 
     border-radius: 5px; 
     cursor: pointer; 
     font-size: 25px; 
 }
 
 .submit-button:hover {
     background-color: #3f6f68; 
 }
 
 .links {
     margin-top: 30px; 
 }
 
 .links a {
     color: #4f8578; 
     text-decoration: none; 
 }
 
 .links a:hover {
     text-decoration: underline; 
 }
 </style>
 
