<template>
  <div class="membership-purchase-page">
    <!--  1      -->
    <LogoHeader />

    <!--  ,    -->
    <div class="menu">
      <div class="menu-item" :class="{ 'active-menu-item': activeMenu === '/membership/info' }" @click="navigateTo('/membership/info')"> </div>
      <div class="menu-item" :class="{ 'active-menu-item': activeMenu === '/membership/purchase' }" @click="navigateTo('/membership/purchase')"> </div>
    </div>
  
    <!--     -->
    <div class="membership-purchase-content">
      <p class="sub-title">2024/25   </p>
      <div class="membership-info-layout">
        <table class="membership-info-table">
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th> </th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>GOLD</td>
              <td>
                  <p>  </p>
                  <p> 7 </p>
                  <p>   ()</p>
                  <p> (MMS , ) </p>
              </td>
              <td>300,000</td>
            </tr>
            <tr>
              <td>SILVER</td>
              <td>
                  <p> 7 </p>
                  <p> (MMS , ) </p>
              </td>
              <td>100,000</td>
            </tr>
          </tbody>
        </table>
      </div>
      
        <!--   -->
        <div class="membership-selection">
          <p class="sub-title"> </p>
          <select class="select-form" v-model="selectedMembership" @change="updatePrices">
            <option value=""></option>
            <option value="silver-100000">SILVER</option>
            <option value="gold-300000">GOLD</option>
          </select>
        </div>

      <div class="charge">
          <div class="charge-money">
                <p><strong> </strong></p>
                <p> : </p>
                <p>{{ new Intl.NumberFormat('ko-KR').format(balance) }} </p>
          </div>
          <button class="btn" id="btn-charge" v-if="leftMoney < 0" @click="openRechargeWindow"></button>
          <div class="membership-price">
              <p></p>
              <p>- {{ payment }} </p>
          </div>
          <div class="left-money">
              <p></p>
              <!-- leftMoney  0  'negative'   -->
              <p :class="{ 'negative': leftMoney < 0 }">{{ formattedLeftMoney }} </p>
          </div>
      </div>

      <div class="buttons">
        <button class="btn" id="btn-purchase" @click="purchaseMembership" v-if="leftMoney >= 0"></button>
      </div>
    </div>
  </div>
</template>

<script>
import LogoHeader from '../common/LogoHeader.vue';
import axios from 'axios';
export default {
    name: 'MembershipPurchase',
    components: {
      LogoHeader
    },    
    props: {
      balance: {
        type: Number,
        required: true
      },
      membership: {
        type: String,
        required: true
      },
    },
  data() {
    return {
      activeMenu: this.$route.path, //   
      //   
      selectedMembership: '',   //   
    };
  },
  watch: {
    //    activeMenu 
    $route(to) {
      this.activeMenu = to.path;
    }
  },
  computed: {
    payment(){
      if(this.selectedMembership!=''){
        return Number(this.selectedMembership.split('-')[1]).toLocaleString('ko-KR');
      }
      return 0;
    },
    leftMoney() {
      return this.balance - Number(this.selectedMembership.split('-')[1]);
    },
    formattedLeftMoney(){
      if(this.selectedMembership!=''){
        return this.leftMoney.toLocaleString('ko-KR');
      }
      return this.balance.toLocaleString('ko-KR');
    }
  },
  methods: {
    navigateTo(route) {
      this.$router.push(route);
      this.activeMenu = route; //      
    },
    
    //      
    async purchaseMembership() {
        let purchaseInfo ={
          email: sessionStorage.getItem('email'),
          membership: this.selectedMembership.split('-')[0]
        }
        try {
            await axios.post("/membership/purchase", purchaseInfo);
            this.$emit('getMembership', this.selectedMembership.split('-')[0]);
            this.$emit('getBalance', this.leftMoney);
            alert('  ');
            this.$router.push({ path: '/myPage/membership' }); //  
        } catch (error) {
            const errorMessage = error.response ? error.response.data : error.message;
            console.error('Error purchasing membership:', errorMessage); //   
            alert('  : ' + errorMessage); //     
        }

      },
    openRechargeWindow() {
      const width = 570;
      const height = 275;
      const left = (window.screen.width / 2) - (width / 2); //   
      const top = (window.screen.height / 2) - (height / 2);
      window.open(`/myPage/recharge`, '', 
      `width=${width},height=${height},,top=${top},left=${left},
      toolbar=no,menubar=no,scrollbars=no,resizable=no,fullscreen=no`);
    },
  }
};
</script>

<style>
.membership-purchase-page {
    padding-top: var(--header-height);
    padding-bottom: var(--footer-height);
    text-align: center;
}

/*  */
.menu {
  display: flex;
  justify-content: center;
  gap: 50px;
  padding-bottom: 20px;
}

.menu-item {
  width: 120px;
  padding: 10px 0;
  position: relative;
  cursor: pointer;
  font-weight: bold;
  color: #565656;
}

.menu-item::after {
  content: "";
  position: absolute;
  bottom: -5px; /*       */
  left: 0;
  width: 100%;  /*   div   */
  height: 3px;
  background-color: black;
  transform: scaleX(0); /*    */
  transition: transform 0.3s ease;
}

.active-menu-item {
  color: black;  /*     black  */
}

.active-menu-item::after {
  transform: scaleX(1);  /*     */
}

.menu-item:hover::after {
  transform: scaleX(1);  /*     */
}

/*   */
.sub-title {
font-size: 20px;
font-weight: 700;
}

.membership-selection p {
font-size: 18px;
}

.membership-purchase-content {
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
width: 100%;
max-width: 90%; /*    */
margin: 0 auto;
}

.membership-info-layout {
width: 80%; /*   50%  */
margin-bottom: 30px; /*    */
}

.membership-info-table {
width: 100%; /*     */
margin: 0 auto; /*   */
padding: auto;
font-size: 21;
}
.membership-info-table {
width: 100%;
text-align: center;
margin: auto;
padding: 0;
border-collapse: collapse; /*    */
}

th, td {
text-align: center; /*     */
padding: 10px; /*    */
border: 1px solid #d8d8d8; /*   */
}

.membership-info-table th {
background-color: #d2e5d0;
color: rgb(7, 7, 7);
}

.membership-info-table td {
background-color: #eef1ee;
}
.membership-selection {
display: flex;
margin-top: 0px;
margin-bottom: 30px;
gap: 30px;
text-align: center;
width: 60%;
margin-left: 0px;
justify-content: center;
align-items: center;
}

.select-form {
width: 70%;
text-align: center;
font-size: 16px;
}

.charge {
  border: 1.5px solid #999999;
  padding: 30px;
  width: 60%; /*   50%  */
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: Arial, sans-serif;
  border-radius: 15px;
}

.charge-money {
  display: flex;
  justify-content: space-between;
  width: 100%;
  align-items: center;
  margin-bottom: 25px;
  align-items: flex-start; /*      */
}

.charge-money p {
  margin: 0;
  font-weight: bold;
}

.membership-price, .left-money {
  border: 1px solid #BCE067;
  padding-left: 15px;
  padding-right: 15px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin: 5px 0;
  border-radius: 10px;
}

.left-money{
background-color: #E4F7BA;
border: none;
}
.negative {
  color: red; /*  0     */
  font-weight: 900;
}

.buttons{
margin-top: 30px;
margin-bottom: 50px;
gap: 20px;
}
/*  css */
.btn {
border: none;
/*   */
color: white;
/*   () */
padding: 8px 16px;
/*    ( 12px,  24px) */
text-align: center;
/*    */
text-decoration: none;
/*   */
display: inline-block;
/*     */
font-size: 22px;
/*   */
border-radius: 8px;
/*   */
cursor: pointer;
/*      */
transition: background-color 0.3s;
/*     */
margin-top: 50px;
margin-left: 15px;
margin-right: 15px;
margin-bottom: 50px;

width: 100px;
/*   */
height: 50px;
/*   */
}

#btn-purchase {
width: 140px;
background-color: #4CAF50;  /*   () */
}

#btn-purchase:hover {
background-color: #45a049; /*      */
}

#btn-charge {
width: 80px;
height: 35px;
font-size: 15px;
background-color: #FFBB00;
margin-bottom: 0px;
padding: 4px 8px;
margin: 10px;
margin-top: -15px;
border-radius: 10px;
margin-left: auto;
}

#btn-charge:hover {
background-color: #EDA900; /*      */
}

#btn-cancel {
background-color: #A6A6A6;  /*   () */
}


</style>