<template>
    <!--   -->
    <div id="modal" class="modal" v-if="visible">

        <!--   -->
        <div class="first-modal-content">
            <!--   -->
            <div v-if="loading" class="loading-spinner">
                  ...
            </div>

            <!--   -->
            <!--      -->
            <div class="modal-title" v-if="firstPage">
                <p class="match-info-title"><strong>{{ formatMatchInfo(matchInfo.matchTeam, thisTeam) }}</strong></p>
                <div class="match-info">
                    <p>{{ match.matchStadium }}&nbsp;</p>
                    <p> | &nbsp;</p> 
                    <p v-html="formatDate(match.matchDate)"></p>
                </div>
                <hr class="divider" />
            </div>


            <!--   -->
            <div v-if="firstPage">
        <div class="modal-body">
            <!--   -->
            <div class="modal-ticket-img" v-if="selectedZoneImage">
                <img :src="selectedZoneImage" alt="  " class="stadium-image" />
            </div>

            <!--   -->
            <div class="modal-table-container">
                <table class="modal-table">
                    <thead class="table-theader">
                        <tr>
                            <th></th>
                            <!-- <th></th> -->
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="zone in filteredAvailableSeats" :key="zone.zoneName">
                            <td>
                                <button class="select-zone-btn" @click="toggleZoneSelection(zone)"
                                :style="{ backgroundColor: getButtonColor(zone.zoneName) }">
                                    {{ zone.zoneName }}
                                </button>
                            </td>
                            <!-- <td></td> -->
                            <td>{{ zone.availableSeatAmount }}</td>
                        </tr>

                        <!--      -->
                        <tr v-if="zoneSelection != null">
                            <td colspan="2">
                                <table class="modal-table-hide">
                                    <tbody>
                                        <tr v-for="section in zoneSelection.sections" :key="section.seatId">
                                            <td>{{ section.sectionName }}</td>
                                            <td>
                                                <div class="quantity-selector">
                                                    <button @click="decreaseQuantity(section)" 
                                                            :disabled="section.quantity <= 0">-</button>
                                                    <input type="text" v-model="section.quantity" readonly class="quantity-box" />
                                                    <button @click="increaseQuantity(section, section.availableSeatAmount)">
                                                        +
                                                    </button>
                                                </div>
                                            </td>
                                            <td>{{ section.availableSeatAmount }}</td>
                                        </tr>
                                    </tbody>
                                </table>
                            </td>
                        </tr>

                    </tbody>
                </table>

            </div>
        </div>
        <hr class="divider" />

        <div class="result-text">
                .
            <div>
                  : {{ sectionSelection ? sectionSelection.sectionName : ' ' }} {{ countTicket }}
            </div>
            <div class="result-text-all">
                 : {{ countTicket }}
            </div>
        </div>
        <div class="first-button">
            <!-- <button class="hiden-button" ref="autoClickButton" @click="handleClick"></button> -->
            <!--   -->
            <button class="buy-button" @click="openSecondPage"></button>

            <!--   -->
            <button class="close-button" @click="closeModal"></button>
        </div>
    </div>
            <!-- ------------------------------------------------------------------ -->

            <!-- 2   -->
            <div v-if="secondPage" class="second-modal-content">
                <div class="modal-body">
                    <div class="left-body">
                            <!--   -->
                    <div class="second-modal-left">
                     <div class="modal-second-title">
                        <p id="second-modal-match-info-title"><strong>{{ formatMatchInfo(matchInfo.matchTeam, thisTeam) }}</strong></p>
                        <div id="second-modal-match-info">
                            <p>{{ match.matchStadium }}&nbsp;</p>
                            <p> | &nbsp;</p> 
                            <p v-html="formatDate(match.matchDate)"></p>
                        </div>
                    </div>
                    <!--   -->
                    <div class="left-table">
                        <div class="modal-table-container">
                            <table class="modal-table">
                                    <tbody>
                                        <tr class="table-theader">
                                            <th></th>
                                            <th></th>
                                            <th></th>
                                            <th></th>
                                        </tr>
                                        <tr>
                                            <td> {{ seatSelection.sectionName }} </td>
                                            <td> {{ seatSelection.quantity }}</td>
                                            <td> {{ seatSelection.seatPrice }}</td>
                                            <td> {{ userMembership.membershipDiscount }} %</td>
                                        </tr>
                                        <tr class="table-theader">
                                            <th colspan="4"></th>
                                        </tr>
                                        <tr>
                                            <td colspan="4">{{ formattedTotal }}</td>
                                        </tr>
                                    </tbody>
                            </table>

                            <div class="modal-info-box">
                                <div class="icon">
                                    <img src="@/assets/img/anyImg/bell-icon.png" alt="alert" />
                                </div>
                                <div class="info-content">
                                    <h4> </h4>
                                    <ul class="notice">
                                        <li> -     1  </li>
                                        <li> -           .</li>
                                    </ul>
                                    <h4> </h4>
                                    <ul class="notice">
                                        <li> -    12  </li>
                                    </ul>
                                </div>
                            </div>
                    </div>
                     
                        </div>
                        </div>
                    </div>

                <div class="vertical-divider"></div>
                    
                <div class="right-table">
                   
                    <div class="ticket-info-content">
                        <p><strong>[]</strong></p>
                        <p>: {{ formatMatchInfo(matchInfo.matchTeam, thisTeam) }}</p>
                        <p>: {{ match.matchStadium }}</p>
                        <p v-html="`: ${formatDate(match.matchDate)}`"></p>
                        <p>: <strong>{{ seatSelection.sectionName }}</strong> / <strong>{{ seatSelection.quantity }}</strong></p>
                    </div>
                    <hr class="divider" />

                    <div>
                        <table class="second-modal-table" id="second-modal-ticke-price">
                            <tbody>
                                <tr>
                                    <td> </td>
                                    <td>
                                        {{ formattedTotal }}
                                    </td>
                                </tr>
                                <tr>
                                    <td> </td>
                                    <td>
                                        {{ formattedMembershipType }} | {{ userMembership.membershipDiscount }} %
                                    </td>
                                </tr>
                                <tr>
                                    <td>  </td>
                                    <td>
                                        {{ formattedPayment.payment }}
                                    </td>
                                </tr>
                            </tbody>
                            
                        </table>
                        <br>
                        <div class="charge">
                            <div class="charge-money">
                                    <p><strong>  : </strong></p>
                                    <p>{{ balance }} </p>
                            </div>
                            <button id="btn-charge" v-if="payment.leftMoney < 0" @click="openRechargeWindow"></button>
                            <div class="membership-price">
                                <p></p>
                                <p>- {{ formattedPayment.payment }} </p>
                            </div>
                            <div class="left-money">
                                <p></p>
                                <!-- leftMoney  0  'negative'   -->
                                <p :class="{ 'negative': payment.leftMoney < 0 }">{{ formattedPayment.leftMoney }} </p>
                            </div>
                        </div>

                    </div>
                    <div>
                        <p class="p">
                             : {{ calculateCancellationDeadline(match.matchDate) }}<br>
                            <!--  -->
                             : 
                            <router-link to="/ticket/info">[]</router-link>
                        </p>
                    </div>
                    <div class="second-button">
                        <!--   -->
                        <button v-if="payment.leftMoney >= 0" class="buy-button" @click="purchaseTicket"> </button>
                        <!--   -->
                        <button class="close-button" @click="openFirstPage"></button>
                    </div>
                    </div>
                
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
export default {
    name: 'TicketModal',
    props: {
        visible: {
            type: Boolean,
            default: false,
        },
        match: {
            type: Object,
            required: true,
        },
        balance: {
            type: Number,
            required: true,
        },
    },
    computed: {
        selectedZones() {
            //  1   
            return this.availableSeatsList
                .filter(zone => zone.availableSeatAmount > 0 && zone.isChecked)
                .map(zone => ({
                    ...zone,
                    sections: zone.sections || [],
                    quantity: zone.sections.reduce((total, section) => total + (section.quantity || 0), 0), //   
                    price: zone.price * zone.quantity //  
                }));
        },
        filteredAvailableSeats() {
            // userMembership.membershipType GOLD  GOLD  ,   GOLD  
            return this.availableSeatsList.filter(zone => 
                this.userMembership.membershipType === 'GOLD' || 
                (zone.availableSeatAmount > 0 && zone.zoneName !== 'GOLD')
            );
        },

        formattedTotal() {
            //       
            return this.total.toLocaleString();
        },

        formattedPayment() {
        //     
            return {
                payment: this.payment.payment.toLocaleString(),
                leftMoney: this.payment.leftMoney.toLocaleString()
            };
        },
        formattedMembershipType() {
            const membershipType = this.userMembership.membershipType;
            // '/'       
            const match = membershipType.match(/\/.{3}(.+)/);
            return match ? match[1].trim() : '';
        }

    },

    watch: {
        //    ,   0 activeButtonIndex 
        zones: {
            handler() {
                if (this.zones.every(zone => zone.quantity === 0)) {
                    this.activeButtonIndex = null;
                }
            },
            deep: true
        },
        autoClick: {
            immediate: true, //    
            handler(newValue) {
                if (newValue) {
                // this.handleClick(); //     
                this.autoClick = false; //      
                }
            }
        }
    },
    mounted() {
        this.fetchEvents();
    },
    data() {
        return {
            loading: true,
            autoClick: true, //     
            //   
            firstPage: true,
            secondPage: false,

            //   
            ticketSalesDto: {},
            matchInfo: {},
            availableSeatsList: [],
            userMembership: {},
            seatDTOList: [],

            //   
            zones: [],  //  
            zoneSelection: null,
            sectionSelection: null,
            selectedZoneImage: require('@/assets/img/stadium/stadium-main.jpg'),
            //  
            countTicket: 0,
            selectedZone: null,
            seatSelection : {
                seatId : null,
                sectionName : '',
                seatPrice: null,
                quantity: 0
            },
            total: 0,
            payment: {},
            thisTeam: "GS ITM",
        };
    },

    methods: {
        // async handleClick() {
        //     console.log(" !");
        //     //       
        //     await this.fetchEvents(); //  fetch
        // },
        // API 
        async fetchEvents() {
            // let data = {};
            try {
                this.loading = true; //      
                console.log("Sending request with:", {
                    email: sessionStorage.getItem('email'),
                    matchId: this.match.matchId
                });
                const response = await axios.get('/ticket/purchase/modal', {
                    params: {
                        email:  sessionStorage.getItem('email'),
                        matchId: this.match.matchId
                    }
                });
                this.ticketSalesDto = response.data.ticketSalesDto;
                this.userBalance = response.data.userBalance;
                this.matchInfo = response.data.matchInfo;
                this.availableSeatsList = response.data.availableSeatsList;
                this.userMembership = response.data.userMembership;
                this.seatDTOList = response.data.seatDTOList;
                this.initializeSectionQuantities(); //   
                console.log(response.data);
                console.log("response:", response);
                // response.data = response.data;
            } catch (error) {
                console.error("Error fetching data: ", error);
            } finally {
                this.loading = false; //      
            }
        },
        //      
        async purchaseTicket(){
            this.ticketSalesDto = {
                userEmail:  sessionStorage.getItem('email'),
                matchId: this.matchInfo.matchId,
                ticketDetailAmount: this.seatSelection.quantity,
                ticketDetailSeat: this.seatSelection.seatId,
                ticketPaidPrice: this.payment.payment,
                tickeId: null,
                ticketNumber: null,
                ticketCreateAt: new Date().toISOString(), //   
            };
            // axios 
            try {
                const response = await axios.post("/ticket/purchase/completed", this.ticketSalesDto);
                console.log(response.data);
                alert('  ');
                this.$router.push({ path: '/myPage/reservations' }); //  
            } catch (error) {
                const errorMessage = error.response ? error.response.data : error.message;
                console.error('Error purchasing ticket:', errorMessage); //   
                alert('  : ' + errorMessage); //     
            }
        },
        //     
        formatMatchInfo(matchTeam, thisTeam) {
            return matchTeam === "" ? `${thisTeam} VS ${matchTeam}` : `${matchTeam} VS ${thisTeam}`;
        },
        //   
        formatDate(date) {
            const options = { year: 'numeric', month: '2-digit', day: '2-digit' };
            const dateObj = new Date(date);
            //  
            const formattedDate = dateObj.toLocaleDateString('ko-KR', options);
            //  
            const days = ['', '', '', '', '', '', ''];
            const dayName = days[dateObj.getDay()];
            //  
            const formattedTime = dateObj.toLocaleTimeString('ko-KR', {
            hour: '2-digit',
            minute: '2-digit'
            });
            return `${formattedDate} (${dayName}) ${formattedTime}`; // <br> 
        },
        // 
        calculateTotal() {
            this.total = this.seatSelection.seatPrice * this.seatSelection.quantity;
        },
        //  
        calculatePayment() {
            this.payment.payment = this.total * (100 - this.userMembership.membershipDiscount) * 0.01;
            this.payment.leftMoney = this.userBalance - this.payment.payment;
        },
        //     
        createSeatSelection(){
            const matchingSeat = this.seatDTOList.find(seat => seat.seatId === this.sectionSelection.seatId);
            const seatPrice = matchingSeat ? matchingSeat.seatPrice : 0; //      0
            this.seatSelection = {
                seatId: this.sectionSelection.seatId,
                sectionName: this.sectionSelection.sectionName,
                quantity: this.countTicket,
                seatPrice: seatPrice
            };
        },
        //   
        openFirstPage() {
            this.firstPage = true;
            this.secondPage = false;
            this.total = 0;
        },

        //   
        openSecondPage() {
            this.createSeatSelection();
            this.calculateTotal();
            this.calculatePayment();
            if (this.total > 0) {
                this.firstPage = false;
                this.secondPage = true;
            } else {
                alert(" .");
            }
        },

        increaseQuantity(section) {
            //    
            this.zoneSelection.sections.forEach(sec => {
                if (sec !== section) {
                    sec.quantity = 0; //     
                    this.countTicket = 0;
                }
            });

            if (section.quantity == 4 && section.availableSeatAmount > section.quantity) {
                this.countTicket=section.quantity;
                this.sectionSelection = section; //    
            }

            if (section.quantity < 4 && section.availableSeatAmount > section.quantity) {
                section.quantity++;
                this.countTicket=section.quantity;
                this.sectionSelection = section; //    
            }
            //     
            const sectionName =section.sectionName;
            const firstChar = sectionName.charAt(0).toUpperCase(); //  
            const secondChar = sectionName.split('/')[1]?.charAt(0).toUpperCase(); // '/'   
            console.log(`  : ${firstChar}${secondChar}`);
            //   
            if (secondChar) {
                this.selectedZoneImage = require(`@/assets/img/stadium/stadium-${firstChar}${secondChar}.jpg`); //  
            } else {
                this.selectedZoneImage = require('@/assets/img/stadium/stadium-main.jpg'); //  
            }
        },
        decreaseQuantity(section) {
            if (section.quantity > 0) {
                section.quantity--;
                this.countTicket--;
            }
            //     
            const sectionName =section.sectionName;
            const firstChar = sectionName.charAt(0).toUpperCase(); //  
            const secondChar = sectionName.split('/')[1]?.charAt(0).toUpperCase(); // '/'   
            console.log(`  : ${firstChar}${secondChar}`);
            //   
            if (secondChar) {
                this.selectedZoneImage = require(`@/assets/img/stadium/stadium-${firstChar}${secondChar}.jpg`); //  
            } else {
                this.selectedZoneImage = require('@/assets/img/stadium/stadium-main.jpg'); //  
            }
        },
        toggleZoneSelection(zone) {
            // 
            this.initializeSectionQuantities();
            if (this.zoneSelection && this.zoneSelection.zoneName === zone.zoneName) {
                //  
                this.zoneSelection = null;
                this.sectionSelection = null;
                this.countTicket = 0;
            } else {
                //   
                this.zoneSelection = zone;
            }
        },
        toggleSectionQuantity(section) {
            // 
            this.initializeSectionQuantities();
            //          0 
            this.zoneSelection.sections.forEach(sec => {
                if (sec !== section) {
                    sec.quantity = 0; //     
                    this.countTicket = 0;
                }
            });
            this.sectionSelection = section; //   
        },
        onCheckboxChange(zone) {
            // 
            this.initializeSectionQuantities();
            if (this.isChecked) {
                this.showDetails(zone);
            } else {
                this.hideDetails();
            }
        },
        showDetails(zone) {
            this.zoneSelection = zone;
        },
        hideDetails() {
            this.countTicket = 0; //   
            this.zones.forEach(zone => {
                zone.quantity = 0; //    0 
            });
            this.selectedZoneImage = require('@/assets/img/stadium/stadium-main.jpg'); //  
        },
        initializeSectionQuantities() {
            //countTicket 
            this.countTicket = 0;
            //    
            this.availableSeatsList.forEach(zone => {
                zone.sections.forEach(section => {
                    section.quantity = 0; //  
                });
            });
        },
        //   
        closeModal() {
            this.hideDetails();
            this.isChecked = false;
            this.isModalOpen = false;
            this.firstPage = true;
            this.secondPage = false;
            this.$emit('close'); //    
        },
        openModal() {
            this.isModalOpen = true;
        },
        //    
        getButtonColor(zoneName) {
            switch (zoneName) {
                case 'GOLD':
                    return '#FFE400';
                case 'BLUE':
                    return '#3162C7';
                case 'AWAY':
                    return '#8C8C8C';
                case 'EAST':
                    return '#993800';
                default:
                    return '#47C83E'; //  
            }
        },

        //  
        calculateCancellationDeadline(date) {
        const dateObj = new Date(date);
        dateObj.setHours(0, 0, 0, 0); //   12 
        return dateObj.toLocaleString('ko-KR', {
            year: 'numeric',
            month: '2-digit',
            day: '2-digit',
            hour: '2-digit',
            minute: '2-digit'
        });
    },
    openRechargeWindow() {
      const width = 570;
      const height = 275;
      const left = (window.screen.width / 2) - (width / 2); //   
      const top = (window.screen.height / 2) - (height / 2);
      window.open(`/myPage/recharge`, '',
      `width=${width},height=${height},,top=${top},left=${left},
      toolbar=no,menubar=no,scrollbars=no,resizable=no,fullscreen=no`);
    },

    },
}
</script>


