<template>
  <div class="membership-view-page">
    <!--  1      -->
    <LogoHeader />

    <!--  ,  ,   -->
    <div class="menu">
      <div class="menu-item" :class="{ 'active-menu-item': activeMenu === '/myPage/reservations' }" @click="navigateTo('/myPage/reservations')"> </div>
      <div class="menu-item" :class="{ 'active-menu-item': activeMenu === '/myPage/membership' }" @click="navigateTo('/myPage/membership')"> </div>
      <div class="menu-item" :class="{ 'active-menu-item': activeMenu === '/myPage/edit-profile' }" @click="navigateTo('/myPage/edit-profile')">  </div>
    </div>

    <!--     -->
    <div class="membership-view-content" v-if="membership">
      <div class="membership-title">
        <h2><strong>{{ name }}</strong>     </h2>
      </div>

      <!-- /   -->
      <div class="membership-detail" v-if="membership !== 'bronze'" :id="membership">
        <h1 class="membership-grade">
          <img :src="membershipImage()" alt="  " class="membershipLevel-image" />
          <span>{{ membership.toUpperCase() }}</span> MEMBERSHIP
          <img :src="membershipImage()" alt="  " class="membershipLevel-image" />
        </h1>
        <div id="membership-price">
          <span class="th"> </span><span class="td"> {{ new Intl.NumberFormat('ko-KR').format(membershipPrice) }}</span>
        </div>
        <div class="membership-status">
          <span class="th"> </span>
          <span class="td" v-if="membershipSalesStatus === 'PURCHASE'"> </span>
          <span class="td" v-else-if="membershipSalesStatus === 'CONFIRMED'"> </span>
          <span class="td" v-else-if="membershipSalesStatus === 'REFUNDED'"> </span>
        </div>
        <div class="membership-sales-date">
          <span class="th"> </span><span class="td">{{ formattedPaymentDate }}</span>
        </div>
        <div class="membership-end-date">
          <span class="th">  </span><span class="td">~ 2025 07 31</span>
        </div>
        <p class="membership-note">
                ,  7     .
        </p>
      </div>
      <div class="cancel-button-box">
        <button @click="MembershipCancel" class="cancel-membership-button" 
          v-if="membershipSalesStatus === 'PURCHASE'"> </button>
      </div>
    </div>
      <!--    -->
      <div class="membership-detail" v-if="membership === 'bronze'" id="bronze">
        <h1 class="membership-grade">
          <img :src="membershipImage()" alt="  " class="membershipLevel-image" />
          <span>{{ membership.toUpperCase() }}</span> MEMBERSHIP
          <img :src="membershipImage()" alt="  " class="membershipLevel-image" />
        </h1>
        <div class="membership-bronze-content" v-if="membership === 'bronze'">
          <p>  .</p>
        </div>
        <div class="button-container">
    <button @click="goToMembershipInfo" class="btn-goToMembershipInfo"> </button>
    <button @click="goToMembershipPurchase" class="btn-goToMembershipPurchase"> </button>
  </div>
      </div>
  </div>
</template>

<script>
import LogoHeader from '../common/LogoHeader.vue';

export default {
  name: 'MembershipView',
  components: {
    LogoHeader,
  },
  props:{
    membership: {
      type: String,
      required: true
    },
  },
  data() {
    return {
      name: 'MMS',
      activeMenu: this.$route.path,
      membershipPrice: null,
      membershipSalesStatus: null,
      membershipSalesCreateAt: null,
      season : '',
    };
  },
  watch: {
    $route(to) {
      this.activeMenu = to.path;
    }
  },
  computed: {
    formattedPaymentDate() {
      if (!this.membershipSalesCreateAt) {
        return '';
      }
      return this.membershipSalesCreateAt.split('T').join(' ').split('.')[0];
    }
  },
  mounted() {
    this.name = sessionStorage.getItem('name');
    this.fetchEvents();
  },
  methods: {
    navigateTo(route) {
      this.$router.push(route);
      this.activeMenu = route;
    },
    goToMembershipInfo() {
      this.$router.push('/membership/info');
    },
    goToMembershipPurchase() {
      this.$router.push('/membership/purchase');
    },
    membershipImage(){
      return  require(`@/assets/img/membershipImg/${this.membership}.png`);
    },
    async fetchEvents() {
      const params = new URLSearchParams();
      params.append('email', sessionStorage.getItem('email'));
      try {
        const response = await this.$axios.post("/myPage/membership", null, {params: params});
        this.membershipPrice = response.data.membershipPrice || ' ',
        this.membershipSalesStatus = response.data.membershipSalesStatus || '  ',
        this.membershipSalesCreateAt = response.data.membershipSalesCreateAt || ' '
        this.$emit("getMembership",response.data.membershipName.split('-')[1]);
        this.season=response.data.membershipName.split('-')[0]
      } catch (error) {
        console.error('   :', error);
      }
    },
    MembershipCancel(){
    const params = new URLSearchParams();
    params.append('userEmail', sessionStorage.getItem('email'));
    this.$axios.post("/membership/cancel", null, { params: params})
    .then(()=>{
      this.$emit("getMembership",'bronze');
      this.$emit("getBalance", Number(sessionStorage.getItem('balance'))+this.membershipPrice);
      alert(`  .\n${new Intl.NumberFormat('ko-KR').format(this.membershipPrice)}  .`);
      this.membershipSalesStatus=null;
    }).catch((error)=>{
      console.log("     : "+ error);
    });
  }
}
}
</script>

<style scoped>
/* General Page Styling */
/* General Page Styling */
.membership-view-page {
  padding-top: var(--header-height);
  padding-bottom: var(--footer-height);
  text-align: center;
}

/* Menu Styling */
.menu {
  display: flex;
  justify-content: center;
  gap: 50px;
  padding-bottom: 20px;
}

.menu-item {
  width: 120px;
  padding: 10px 0;
  cursor: pointer;
  font-weight: bold;
  color: #565656;
  position: relative;
  transition: color 0.3s ease;
}

.menu-item::after {
  content: "";
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: black;
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.menu-item:hover {
  color: #333;
}

.menu-item:hover::after,
.active-menu-item::after {
  transform: scaleX(1);
}

.active-menu-item {
  color: black;
}

.membership-detail {
  margin: auto;
  max-width: 600px;
  border-radius: 10px;
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.3);
  text-align: center;
  height: auto;
}
/* Gold Membership Detail */
#gold {
  border: 2px solid #E1B12C;
  background-color: #f3efe6a9;
}

/* Silver Membership Detail */
#silver {
  border: 2px solid #B0BEC5;
  background-color: #f8f8f8;
}

/* Membership Grade */
.membership-grade {
  padding: 10px;
  margin-bottom: 0px;
  margin-top: 0;
  text-align: center;
  justify-content: center;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}
#gold .membership-grade {
  border-bottom: 2px solid #D4AF37;
  color: #B8860B;
  background-color: #fff5aa;
}

#silver .membership-grade {
  border-bottom: 2px solid #B0BEC5;
  color: #607D8B;
  background-color: #E0E0E0;
}

/* Membership Details:   */
.membership-detail div {
  display: flex;
  padding: 10px 0;
  margin: 0px 30px; 
  position: relative;
  align-items: center; /*    */
}

#gold div::before {
  background-color: #E1B12C; /*   */
  content: "";
  position: absolute;
  left: 35%;
  top: 0;
  bottom: 0;
  width: 1px;
  transform: translateX(-50%);
}

#silver div::before {
  background-color: #B0BEC5; /*   */
  content: "";
  position: absolute;
  left: 35%;
  top: 0;
  bottom: 0;
  width: 1px;
  transform: translateX(-50%);
}

/* Gold Membership Detail -   */
#gold #membership-price,
#gold .membership-status,
#gold .membership-sales-date,
#gold .membership-end-date {
  border-bottom: 1px solid #E8C267;
}

/* Silver Membership Detail -   */
#silver #membership-price,
#silver .membership-status,
#silver .membership-sales-date,
#silver .membership-end-date {
  border-bottom: 1px solid #B0BEC5;
}

.membership-detail .th {
  font-weight: bold;
  width: 35%;
  text-align: right; 
  padding-right: 30px; 
}
#gold .membership-detail .th {
  color: #8B4513; 
}
#silver .membership-detail .th {
  color: #607D8B
}

.membership-detail .td {
  font-size: 20px;
  color: #3B2F2F; 
  width: 65%;
  text-align: center; 
}
#gold .membership-detail .td {
  color: #3B2F2F; 
}
#silver .membership-detail .td {
  color: #607D8B; 
}
/* Membership Note:  */
.membership-note {
  font-size: 14px;
  margin-top: 10px;
  color: #b9b9b9;
}

/* Bronze Membership Info */
.membership-info-bronze {
  background-color: #E0F7FA;
  margin: auto;
  padding: 20px;
  max-width: 600px;
  border-radius: 10px;
  border: 2px solid #009688;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
  text-align: center;
  height: auto;
}
#bronze {
  border: 2px solid #B87333;
  background-color: #f2e9e0a7;
  margin-bottom: 30px;
}
#bronze .membership-grade {
  border-bottom: 2px solid #CD7F32;
  color: #8C4B1F; 
  background-color: #FAE3D2;
  font-size: 1.8em;
}
.membership-bronze-content{
  justify-content: center;
}
.membership-bronze-content p {
  color: #5A3E2B;
  font-size: 25px;
}
/* Cancel Membership Button */
.cancel-button-box {
  text-align: right;
  max-width: 600px;
  margin: auto;
}
/*    */
.button-container {
  display: flex;
  justify-content: center;
  gap: 30px; /*     */
  margin-right: 30px;
}

/*     */
.btn-goToMembershipInfo,
.btn-goToMembershipPurchase {
  background-color: #B87333; /*   */
  color: #fff;
  border: 2px solid #CD7F32;
  border-radius: 8px;
  cursor: pointer;
  padding: 15px 30px;
  font-weight: bold;
  font-size: 1.2em; /*    */
  transition: background-color 0.3s ease, box-shadow 0.3s ease;
}

/*     */
.btn-goToMembershipInfo:hover,
.btn-goToMembershipPurchase:hover {
  background-color: #CD7F32; /*      */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2); /*    */
}
.cancel-membership-button {
  background-color: #f3aca9;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  font-size: large;
  width: 100px;
  height: 40px;
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 30px;
}

.cancel-membership-button:hover {
  background-color: #d32f2f; /*    */
}

/* Membership Level Image */
.membershipLevel-image {
  width: 40px;
  vertical-align: middle;
  filter: drop-shadow(2px 2px 2px rgba(0, 0, 0, 0.2)); /*    */
}
</style>
