<template>
    <!-- Web  http://localhost:8080  -->
    <!-- JavaScript  174d633fdbdf8cc840d5ab1909104e05 -->
    <div id="map"></div>
</template>

<script>
export default {
    name: "KaKaoMap",
    data() {
        return {
            map: null,
        };
    },
    mounted() {
        if (window.kakao && window.kakao.maps) {
        //   ,       
            this.loadMap();
        } else {
            //       
            this.loadScript();
        }
    },
    methods: {
        // api 
        loadScript() {
        const script = document.createElement("script");
        script.src =
            "//dapi.kakao.com/v2/maps/sdk.js?appkey=174d633fdbdf8cc840d5ab1909104e05&autoload=false"; // &autoload=false api       
        script.onload = () => window.kakao.maps.load(this.loadMap); //           

        document.head.appendChild(script); // html>head    
        },
        loadMap() {
            const container = document.getElementById("map"); //   DOM 

            // EPSG:5181  WGS84 
            const coords = new window.kakao.maps.Coords(497180.0, 1131209.0);
            const wgs84Coords = coords.toLatLng(); // WGS84  

            const options = {
                //      
                center: wgs84Coords, //  WGS84  
                level: 4, //  (,  )
            };

            this.map = new window.kakao.maps.Map(container, options); //     
            this.loadMaker(wgs84Coords); //   
        },
        //    
        loadMaker(coords) {
        const markerPosition = coords; //     

        const marker = new window.kakao.maps.Marker({
            position: markerPosition,
        });

        marker.setMap(this.map);
        },
    }
}
</script>

<style scoped>
#map {
    width: 800px;
    height: 400px;
}
</style>