  cursor: pointer;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}

/* Total   */
/*    ,     */
.header-controls {
display: flex;
justify-content: center;
align-items: center;
}

.nav-button {
width: 40px;
height: 40px;
margin: 0 20px;
cursor: pointer;
}

