  /*       */
  margin-right: 10px;
}

select {
  -webkit-appearance: none; /*    */
  -moz-appearance: none; /* Firefox    */
  appearance: none; /*    */

  border: 1px solid #ddd;
  background-color: #f8f9fa;
  border: none;
  box-shadow: 0 2px 4px rgba(50, 50, 50, 0.2);
  /* padding: 10px; */
  padding: 10px 30px;
  padding-right: 40px;

  border-radius: 5px;
  font-size: 18px;
  font-weight: bold;
  color: #333;
  outline: none;
  margin: 0 5px;
  cursor: pointer;

  background-image: url('@/assets/img/game/select-icon-down.png'); /*    */
  background-repeat: no-repeat;
  background-position: right 15px center; /*   */
  background-size: 12px; /*   */
}

/*     */
.ticket-purchase-btn {
  background-color: #f8f9fa;
  border: 1px solid #ddd;
  border: none;
  box-shadow: 0 2px 4px rgba(50, 50, 50, 0.2);
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 16px;
  font-weight: bold;
  color: #333;
  cursor: pointer;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}

.ticket-purchase-btn:hover {
  transform: translateY(-50%) scale(1.05);
