}

/* Calendar Table   */
/*   */
.calendar-table {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  border-collapse: collapse;
  height: 100%;
}

.calendar-table thead {
  height: 50px; /*    */
  border-top: 2px solid #4f8578;
  border-bottom: 1px solid #ddd;
}

.calendar-table th {
  height: 50px; /*     */
  text-align: center;
  vertical-align: middle;
}

.calendar-table tbody {
  height: 1029px; /*    */
}

.calendar-table td {
  position: relative;
  width: 170px;
  height: 180px;
  border: 1px solid #ddd;
  text-align: center;
  vertical-align: middle;
}

.day-number {
  position: absolute;
  top: 10px;
  left: 17px;
  font-size: 14px;
  color: #333;
}

/*    */
