background-color: #afafaf;
}

/*    */
.result {
  font-size: 14px;
  text-align: center;
  align-items: center;
}
/*   */
.score {
  font-size: 16px;
  font-weight: bold;
  margin-left: 10px;
}

/*   */
.reservation-button {
padding: 10px 20px;
background-color: #fff;
